import Table from '../../mo/models';
import TableCell from '../../mo/models/tableCell';

export default function traverseSingleColCells(
  table: Table,
  targetColIndex: number,
  triggerColIndex: number,
  callback: (
    rowIndex: number,
    finalIndex: number,
    trggerCell: TableCell,
    originalCell: TableCell,
  ) => void,
) {
  for (let rowIndex = 0; rowIndex < table.nodes.length; rowIndex += 1) {
    const finalIndex = Math.min(targetColIndex, table.nodes[rowIndex].nodes.length);
    const row = table.nodes[rowIndex];
    if (!row) {
      return;
    }
    const originalCell = row.nodes[finalIndex];
    const triggerCell = row.nodes[triggerColIndex];
    callback(rowIndex, finalIndex, triggerCell, originalCell);
  }
}